body {
  background: #edeef0; }
  body[theme="night"] {
    background: #414141; }
    body[theme="night"] * {
      color: #ffffff; }

.wrap {
  width: 100%;
  max-width: 992px;
  padding: 0px 40px 0px 40px;
  overflow-x: hidden; }
  @media (max-width: 1340px) {
    .wrap {
      padding: 0px 20px 0px 20px; } }

.content {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  width: 100%; }
  .content-wrap {
    display: flex;
    justify-content: center;
    padding-bottom: 40px; }
    @media (max-width: 600px) {
      .content-wrap {
        padding-bottom: 70px; } }
  .content-right {
    width: calc(100% - 150px);
    padding-left: 40px; }
    @media (max-width: 1340px) {
      .content-right {
        padding-left: 20px; } }
    @media (max-width: 768px) {
      .content-right {
        width: calc(100% - 100px); } }
    @media (max-width: 600px) {
      .content-right {
        padding: 0;
        width: 100%; } }

* {
  box-sizing: border-box;
  font-family: "Open Sans";
  font-weight: 400; }

a {
  text-decoration: none;
  color: black; }

input,
textarea {
  -webkit-appearance: none;
  border: none;
  outline: none;
  resize: none; }

body.login .content-right {
  width: 100%;
  padding-left: 0;
  position: relative; }
  @media (max-width: 1340px) {
    body.login .content-right {
      width: 100%;
      padding-left: 0; } }
  @media (max-width: 768px) {
    body.login .content-right {
      width: 100%;
      padding-left: 0; } }
  @media (max-width: 600px) {
    body.login .content-right {
      padding: 0;
      width: 100%; } }

button {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #333;
  min-height: 30px;
  width: auto;
  padding: 0 15px;
  border: none;
  outline: none;
  color: white;
  border-radius: 3px;
  font-size: 14px;
  font-weight: 600;
  margin-top: 10px;
  line-height: 1;
  opacity: 1;
  transition: opacity .2s; }
  button[disabled] {
    opacity: .3;
    transition: opacity .2s; }

body[theme="night"] button {
  background-color: #ec625f; }
