@import "../../assets/scss/resources.scss";

.wrapper {
  display: flex;
  justify-content: space-between;
  align-items: stretch;
  width: 100%;
  flex-flow: row wrap;
  position: relative;

  &:after {
    content: "";
    width: calc(33.333% - 15px);
    @include r(920) {
      width: calc(50% - 10px);
    }
    @include r(480) {
      width: 100%;
    }
  }
}

.item {
  width: calc(33.333% - 15px);
  position: relative;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 0 0 #d7d8db, 0 0 0 1px #e3e4e8;
  margin: 15px 0 0;
  padding: 15px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  flex-grow: 1 auto;
  @include r(920) {
    width: calc(50% - 10px);
  }
  @include r(480) {
    width: 100%;
  }


}

.img {
  min-height: 100px;
  min-width: 100px;
  max-width: 100px;
  border-radius: 100%;
  background-size: cover;
  background-position: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  margin: 0 auto;
}

.name {
  font-size: 18px;
  margin-top: 10px;
  line-height: 1.25;
  text-align: center;
}

.country {
  color: rgba(#000, .6);
  font-size: 14px;
  text-align: center;
}

.status {
  text-align: center;
  font-size: 14px;
  line-height: 1.3;
  margin-top: 5px;
  height: 100%;
}

.selected {
  font-weight: bold;
}



@include nignt {
  .item {
    background: $nightLightGrey;
    box-shadow: 0 1px 0 0 $nightLightGrey, 0 0 0 1px $nightLightGrey;
  }
    .item{
        button{
            background-color: $nightOrange;
        }
    }
}

